<script>
export default {
  components: {},
  data() {
    return {};
  },
  computed: {
    player: function () {
      return this.$store.state.player;
    },
    levelClass: function () {
      if (this.$store.state.animation.levelUp) return "pulsate-bck level"; //on level up animation
